body {
  overflow-y: visible;
}

.card {
  border-radius: 0.5rem !important;
  padding: 1rem !important;
  transition-duration:0.3s;
}
.card:hover {
  box-shadow: 0px 0px 10px 3px rgba(0, 0, 0, 0.25);
  border-radius: 0.5rem !important;
  padding: 1rem !important;
  transform: translate(0,-8px);
}
